<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process268"></a>

<p><br></p>
<p><span class="rvts17">曾经的错误（</span><span class="rvts50">Past Mistakes</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:177,Y:768</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：卡隆</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">杀掉被附身的卡隆</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1481.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts19">该任务需要穿过地下道，来到卡隆被关着的地方，注意到达那里，需要解锁图中的铁门，并跳跃或传送下去。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1482.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts19">卡隆看上去是一个可怜的被关的孩子，但其实他是一个较为厉害的恶魔。在仪式周围，有</span><span class="rvts20">5</span><span class="rvts21">个驱魔者的灵魂，与他们交谈，他们会告诉你卡隆的法力相当厉害。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1483.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts19">在卡隆被关的笼子对面，可以找到一个被木板钉死的门。攻击</span><span class="rvts20">3</span><span class="rvts21">块木板，可以解锁该门，这个门最终通向兵营的监狱中。解锁该木门，会使得卡隆瞬间逃跑消失。</span>
<br><span class="rvts21">然而，如果在该门被拆封前，我们无法从兵营来到这里。也就是说，只能通过地下道来到这里。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1484.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts19">仪式中心会出现</span><span class="rvts20">5</span><span class="rvts21">个油锅，使用火焰技能，一次性点亮所有油锅，卡隆的邪恶化身被召唤出来，接下来就是一场打斗。</span>
<br><span class="rvts21">注意，如果你没有拆远处通往兵营的门的话，卡隆会召唤出</span><span class="rvts20">4</span><span class="rvts21">个额外的喽啰与你战斗。如果你拆开了那个被钉的木门，那么在他逃跑后，使用召唤仪式，只能召唤出他一个人，因此也就使得战斗变得简单一些。</span>
<br><span class="rvts21">不过之后，他的</span><span class="rvts20">4</span><span class="rvts21">个喽啰会在将来出现在凯姆洋馆前的损坏大桥处。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



